import React, { useState, useEffect } from 'react';
import Country from './components/Country';
import NewCountry from './components/NewCountry';
import { getMedals } from './services/medalService';
import axios from 'axios';
import Container from '@material-ui/core/Container';
import Badge from '@material-ui/core/Badge';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import './App.css';

const useStyles = makeStyles({
  appHeading: {
    textAlign: 'center',
    margin: 0,
    backgroundColor: '#BBBBF1',
    position: 'fixed',
    width: '100%',
    paddingTop: '15px',
    paddingBottom: '4px',
    zIndex: '1000',
    opacity: '.95',
  },
  appHeadingBadge: {
    fontSize: '2.0em',
    fontFamily: 'Teko, sans-serif',
  },
  appHeadingText: {
    paddingRight: '8px'
  },
});

const App = () => {
  const classes = useStyles();
  const [countries, setCountries] = useState([]);
  const [medals, setMedals] = useState(getMedals());
  const apiEndpoint = "https://medalsapi.azurewebsites.net/api/country";

  const handleAdd = async (name) => {
    const { data: post } = await axios.post(apiEndpoint, { name: name });
    setCountries(countries.concat(post));
  }
  const handleDelete = async (countryId) => {
    const originalCountries = countries;
    setCountries(countries.filter(c => c.id !== countryId));
    try {
      await axios.delete(`${apiEndpoint}/${countryId}`);
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        // country already deleted
        console.log("The record does not exist - it may have already been deleted");
      } else { 
        alert('An error occurred while deleting');
        setCountries(originalCountries);
      }
    }
  }
  const handleIncrement = (countryId, medal) => {
    const mutableCountries = [ ...countries ];
    const idx = mutableCountries.findIndex(c => c.id === countryId);
    mutableCountries[idx][medal + 'MedalCount'] += 1;
    setCountries(mutableCountries);
  }
  const handleDecrement = (countryId, medal) => {
    const mutableCountries = [ ...countries ];
    const idx = mutableCountries.findIndex(c => c.id === countryId);
    if (mutableCountries[idx][medal + 'MedalCount'] === 0) return;
    mutableCountries[idx][medal + 'MedalCount'] -= 1;
    setCountries(mutableCountries);
  }
  const getMedalTotal = (medal) => countries.reduce((a, b) => a + b[medal + 'MedalCount'], 0);
  const getAllMedalsTotal = () => {
    let sum = 0;
    medals.forEach(medal => {
      sum += getMedalTotal(medal.name);
    });
    return sum;
  }
  // this is the functional equivalent to componentDidMount
  useEffect(() => {
    // initial data loaded here
    setMedals(getMedals());
    async function fetchData() {
      const { data: countries } = await axios.get(apiEndpoint);
      setCountries(countries);
    }
    fetchData();
  },[])
  return (
    <React.Fragment>
      <div className={classes.appHeading}>
        <Badge 
          className={classes.appHeadingBadge}
          badgeContent={ getAllMedalsTotal() } 
          color='primary'>
          <div className={classes.appHeadingText}>Olympic Medals</div>
        </Badge>
      </div>
      <Container fixed={true} style={{paddingTop: '70px', paddingBottom: '20px'}}>
        <NewCountry onAdd={ handleAdd } />
        <Grid spacing={2} justifyContent='center' container>
          { countries.map(country =>
            <Grid item key={ country.id }>
              <Country 
                country={country} 
                medals={medals}
                onDelete={ handleDelete }
                onIncrement={ handleIncrement } 
                onDecrement={ handleDecrement } />
            </Grid>
          )}
        </Grid>
      </Container>
    </React.Fragment>
  );
}
 
export default App;
